<template>
  <div id="app">
    <Header />
    <md-field>
      <label>Search</label>
      <md-input v-model="search"></md-input>
      <md-icon>search</md-icon>
    </md-field>
    <Content v-bind:commands="filterCommand" />
  </div>
</template>

<script>
import Header from "./components/Header";
import Content from "./components/Content";
import json from "./json/data.json";

export default {
  name: "App",
  components: {
    Header,
    Content
  },
  data() {
    return {
      search: "",
      commands: json,
      filterCommand: json
    };
  },
  watch: {
    search: function(val) {
      this.filterCommand = this.commands.filter(
        item => item.tag.indexOf(val) !== -1
      );
    }
  }
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin: 0 80px 0 80px;
}
</style>
